<template>
    <div class="point">
        <div class="point-content">
            <div class="table-head">
                <el-button type="default" class="left" :size="inputSize" @click="delMsgFunc()" :disabled="tableData && tableData.hasOwnProperty('list') && tableData.list.length <= 0">{{ $L('选中删除') }}</el-button>
                <div class="right">
                    {{ $L('时间') }}：
                    <el-date-picker
                            style="width: 350px;"
                            v-model="SearchTimeZone"
                            type="datetimerange"
                            align="right"
                            v-bind:size="inputSize"
                            :start-placeholder="$L('开始日期')"
                            :end-placeholder="$L('结束日期')"
                            value-format="yyyy-MM-dd HH:mm:ss"
                            :default-time="['00:00:00', '23:59:59']"
                            @change="SearchTimeFunc">
                    </el-date-picker>
                    <el-select class="search" v-model="messageType" v-bind:size="btnSize"
                               @change="changeTypeFunc">
                        <el-option
                                v-for="item in messageTypelist"
                                :key="item.type"
                                :label="item.name"
                                :value="item.type">
                        </el-option>
                    </el-select>
                    <el-input :placeholder="$L('输入描述')" v-bind:size="btnSize" v-model="inputValue" class="input-with-select"
                              clearable
                              @keyup.enter.native="$event.target.blur"
                              @change="searchFunc">
                        <el-button slot="append" icon="el-icon-search"></el-button>
                    </el-input>
                </div>
            </div>
            <div class="table">

                <div class="content" v-if="tableData.list !== null" v-loading="loading">
                    <el-table
                            :data="tableData.list"
                            style="width: 100%" class="table"
                            :empty-text="$L('暂无数据')"
                            :default-sort ="{prop:'log_id',order:'descending'}"
                            @selection-change="handleSelectionChange"
                            @sort-change="sortChange">
                        <el-table-column
                                type="selection"
                                width="55">
                        </el-table-column>
                        <el-table-column
                                :label="$L('积分类型')"
                                align="left">
                            <div slot-scope="scope">
                                {{textType(scope.row.type,scope.row.point,scope.row.desc).text}}<br>
                                <span v-if="scope.row.orderNum" class="font-grey">{{$L('订单号')}}：{{ scope.row.orderNum }}</span>
                            </div>
                        </el-table-column>
                        <el-table-column
                                prop="point"
                                sortable
                                :label="$L('积分')"
                                align="center">
                            <div slot-scope="scope" class="font-green">
                                {{ scope.row.point }}
                            </div>
                        </el-table-column>
                        <el-table-column
                                prop="createTime"
                                sortable
                                :label="$L('时间')"
                                align="center">
                        </el-table-column>
                        <el-table-column
                                :label="$L('描述')"
                                align="left">
                            <div slot-scope="scope">
<!--                                {{ scope.row.desc }}-->
                                {{textType(scope.row.type,scope.row.point,scope.row.desc).textTwo}}<br>
                            </div>
                        </el-table-column>
                    </el-table>
                    <!-- 分页 -->
                    <div class="list-pages" v-if="tableData.list !== null && tableData.allNum>0">
                        <el-pagination
                                background
                                @size-change="handleSizeChange"
                                @current-change="handleCurrentChange"
                                :current-page="currentPage"
                                :page-size="pageSize"
                                layout="prev, pager, next, total, jumper"
                                :total="tableData.allNum">
                        </el-pagination>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "myPoint",
        data () {
            return {
                loading: true,
                errorType: 'error',
                errorTitle: this.$L('出错了'),
                errorTips: null,
                errorShow: false,
                inputSize:'small',
                pointNum:0,
                btnSize:'small',
                messageTypelist:[
                    {
                        type:'all',
                        name:this.$L('全部积分')
                    },
                    {
                        type:'addPoint',
                        name:this.$L('增积分')
                    },
                    {
                        type:'cutPoint',
                        name:this.$L('减积分')
                    },
                ],
                messageType:'all',
                inputValue:'',
                sortBy:'',
                tableData:{},
                SearchTimeZone:"",
                dtCreateStart:"",
                dtCreateEnd:"",
                pageSize:20,
                currentPage:1,
                msgArr:[],

            }
        },
        created (){
            this.getData();
        },
        methods : {
            showError(title, tips, type) {
                if (type === 'warning') {
                    this.errorType = 'warning';
                } else {
                    this.errorType = 'error';
                }
                if (title && title !== '') {
                    this.errorTitle = title;
                } else {
                    this.errorTitle = this.$L('出错了');
                }
                if (tips && tips !== '') {
                    this.errorTips = tips;
                } else {
                    this.errorTips = null;
                }
                this.loading = false;
                this.errorShow = true;
            },
            getData() {
                const _self = this;
                _self.loading = true;
                let arr = {
                    p : _self.currentPage,
                    num : _self.pageSize,
                    order : _self.sortBy,
                    type : _self.messageType,
                    keyword : _self.inputValue,
                    dtCreateStart:_self.dtCreateStart,
                    dtCreateEnd:_self.dtCreateEnd
                };
                _self.$api('getPointList', arr , function (reData) {
                    if (reData.flag === 1) {
                        _self.pointNum = reData.data.points;
                        _self.tableData = reData.data;
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name: 'home'});
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        _self.$router.push({name: 'home'});
                    });
                });
            },
            pointExchangegFunc(){
                this.$router.push({name:'integralShop'});
            },
            handleCurrentChange(val){
                this.currentPage = val;
                this.getData();
            },
            handleSizeChange(val){
                this.pageSize = val;
                this.getData();
            },
            changeTypeFunc(val){
                this.messageType = val;
                this.getData();
            },
            searchFunc(val){
                this.inputValue = val;
                this.getData();
            },
            SearchTimeFunc(time){
                if(time !== null){
                    this.dtCreateStart = time[0];
                    this.dtCreateEnd = time[1];
                }else{
                    this.dtCreateStart = '';
                    this.dtCreateEnd = '';
                }
                this.getData();
            },
            handleSelectionChange(val){
                let arr = [];
                for (let i in val){
                    arr[i] = val[i].id;
                }
                this.msgArr = arr;
            },
            delMsgFunc(id){

                const _self = this;
                if (!id && (Array.isArray(this.msgArr) && this.msgArr.length === 0)) {

                    //未知错误
                    _self.$confirm(_self.$L('您还没有选中积分记录，请勾选会员信息再做此操作'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'warning',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        _self.$router.push({name: 'pointList'});
                    });
                    return ;
                }

                let groupId;

                if (id) {
                    groupId = id;
                } else {
                    groupId = this.msgArr;
                }

                let arr = {
                    id : groupId
                }
                _self.$confirm(_self.$L('此操作将永久删除该积分记录, 是否继续?'),_self.$L( '提示'), {
                    confirmButtonText:_self.$L( '确定'),
                    cancelButtonText:_self.$L( '取消'),
                    type: 'warning'
                }).then(() => {
                    _self.$api('deletePointMsg', arr, function (reData) {
                        if (reData.flag === 1) {
                            _self.loading = false;
                            _self.$message({
                                type: 'success',
                                message: _self.$L('删除成功!')
                            });
                            _self.getData();
                        } else if (reData.flag === -1) {
                            //登录超时了
                            _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                                confirmButtonText: _self.$L('重新登录'),
                                cancelButtonText: _self.$L('取消'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                            }).catch(action => {
                                _self.$gotoWebHome();
                            });
                        } else {
                            console.log(reData);
                            //未知错误
                            _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                                confirmButtonText: _self.$L('点击重试'),
                                cancelButtonText: _self.$L('返回首页'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.getData();
                            }).catch(action => {
                                _self.$router.push({name: 'home'});
                            });
                        }
                    }, function (error) {
                        console.log(error);
                        //网络请求错误
                        _self.$confirm(_self.$L(error.msg), _self.$L('出错了'), { //todo:msg还未更换语言包语言包
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name: 'home'});
                        });
                    });
                }).catch(() => {

                });
            },
            sortChange(column, prop, order){
                this.sortBy = column.prop;
                this.getData();
            },
            textType(status,point,desc){
                let x;
                switch (status) {
                    case 100:
                        x = {text: this.$L('积分购物'),textTwo: this.$L('积分购物')};
                        break;
                    case 101:
                        x = {text: this.$L('签到积分'),textTwo: this.$L('签到积分')+point};
                        break;
                    case 102:
                        x = {text: this.$L('会员登录积分'),textTwo: this.$L('会员登录积分')};
                        break;
                    case 104:
                        x = {text: this.$L('会员注册'),textTwo: this.$L('会员注册')};
                        break;
                    case 105:
                        x = {text: this.$L('关注微信公众号'),textTwo: this.$L('关注微信公众号')};
                        break;
                    case 106:
                        x = {text: this.$L('发展下级分销商'),textTwo: this.$L('发展下级分销商')};
                        break;
                    case 107:
                        x = {text: this.$L('发展下级会员'),textTwo: this.$L('发展下级会员')};
                        break;
                    case 201:
                        x = {text: this.$L('兑换产品'),textTwo: this.$L('积分商城产品兑换')};
                        break;
                    case 202:
                        x = {text: this.$L('兑换礼品'),textTwo: this.$L('积分商城礼品兑换')};
                        break;
                    case 203:
                        x = {text: this.$L('兑换代金券'),textTwo: this.$L('积分商城优惠劵兑换')};
                        break;
                    case 204:
                        x = {text: this.$L('积分抽奖'),textTwo: this.$L('积分商城积分抽奖')};
                        break;
                    case 205:
                        x = {text: this.$L('积分抵扣金额'),textTwo: this.$L('积分商城积分抵扣金额')};
                        break;
                    case 103:
                        x = {text: this.$L('会员卡获取积分'),textTwo: this.$L('会员卡获取积分')};
                        break;
                    case 108:
                        x = {text: this.$L('连续签到获取积分'),textTwo: this.$L('连续签到获取积分')+point};
                        break;
                    case 1:
                        x = {text: this.$L('管理员录入积分'),textTwo: this.$L('管理员录入积分')};
                        break;
                    case 3:
                        x = {text: this.$L('激活会员卡增加积分'),textTwo: this.$L('激活会员卡增加积分')+point};
                        break;
                    case 5:
                        x = {text: this.$L('初始化增加经验积分'),textTwo: this.$L('初始化增加经验积分')};
                        break;
                    case 0:
                        x = {text: this.$L('订单编号：没有订单信息'),textTwo: desc};
                        break;
                    default:
                        x = {text: this.$L('未知状态'),textTwo: this.$L('未知状态')};
                        break;
                }
                return x;
            },
        }
    }
</script>

<style lang="scss" scoped>
    @import "../../../assets/css/common/common.scss";
    .point{
        position: absolute;
        width: 100%;
        height: auto;
        top: 0;
        left: 0;
        padding: 20px;
        .point-head{
            width: calc(100% - 200px);
            position: fixed;
            top: 50px;
            height: 50px;
            z-index: 999;
            margin: 0 -20px;
            background-color: #fff;
            .left{
                display: inline-block;
                float: left;
                margin-left: 20px;
            }
            .point-num{
                font-size: 18px;
                display: inline-block;
            }
            .right{
                float: right;
                margin-right: 20px;
                margin-top: 7px;
                height: 35px;
            }
        }
        .point-content{
            .table-head{
                height: 32px;
                margin-bottom: 10px;
                .left{
                    float: left;
                }
                .right{
                    float: right;
                    .time-search{
                        vertical-align: bottom;
                    }
                    .font{
                        padding-left: 20px;
                        font-size: 16px;
                    }
                    .search{
                        width: 120px;
                        padding-left: 10px;
                    }
                    .input-with-select{
                        width: 280px;
                        padding-left: 10px;
                    }
                }
            }
            .table{
                padding: 20px;
                background: #fff;

                .font-grey{
                    color: #999;
                }
                .font-green{
                    color: #00bf00;
                }
                .content{
                    background: #fff;
                    .table{
                        padding: 0;
                        .status{
                            .font-red{
                                color: #f00;
                            }
                            .font-grey{
                                color: #999;
                            }
                        }
                        .operate{
                            button{
                                padding: 8px 28px;
                            }
                        }
                    }
                    .list-pages{
                        text-align: center;
                        padding: 40px 0 20px;
                    }
                }
            }
        }
        .error {
            position: absolute;
            z-index: 200;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: #FFFFFF;
            overflow: hidden;

            .error-tips {
                position: absolute;
                top: 50%;
                left: 50%;
                width: 320px;
                transform: translate(-50%, -50%);
            }
        }
    }
</style>